.intro {
    background-color: black;
    width: 100%;
    max-width: 1920px;
    color: white;
    z-index: auto;
    padding-top: 100px;
}

/* main container */

.container {
    display: flex;
    position: relative;
    max-width: 1920px;
}

.text {
    width: 100%;
    margin: 80px 0 0 45px;
    animation: opacity 1.5s linear;
    overflow: hidden;
}

.txt {
    text-align: center;
    font-size: 32px;
    margin: 90px 0 0 30px;
    width: 670px;
    animation: leftAnim 0.7s ease-in-out;
}

.item {
    align-items: center;
}


.angler {
    position: absolute;
    top: 250px;
    left: 30px;
    transform: rotateZ(180deg);
}

.anglel {
    position: absolute;
    top: 570px;
    left: 510px;
}

/* title */

.container.title {
    margin-top: 170px;
    flex-direction: column;
}

.title .txt {
    width: 98%;
    font-size: 55px;
}

.title .anglel {
    top: 60px;
    left: 1400px;
}

.title .angler {
    top: 40px;
    left: 280px;

}

/* hr */

.container.title .txt.up {
    animation: upAnim 0.7s linear;
}

.horizont {
    margin-top: 155px;
    display: flex;
    justify-content: center;
}

.horizont hr {
    width: 300px;
    border: 3px white solid;
}

.hidden {
    visibility: hidden;
}

.horizont hr:nth-child(1) {
    border-radius: 5px 0px 0px 5px;
}

.horizont hr:nth-child(2) {
    border-radius: 0px 5px 5px 0px;
}

.animationWidth {
    visibility: visible;
    animation: width 2s ease-in-out;
}

/* service */

.container.service {
    margin-top: 100px;
    display: flex;
    flex-direction: column;
}

.container.service .serviceBlock {
    width: 100%;
    display: flex;
    margin-top: 80px;

}

.container.service .serviceBlock .serviceItem {
    width: 50%;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
}

.container.service .serviceBlock .serviceItem img {
    opacity: 0.4;
    background-color: black;
    display: flex;
    justify-content: center;
    cursor: pointer;
    transition: all .6s linear;
}

.container.service .serviceBlock .serviceItem:hover img {
    transform: scale(1.1);
    transition: all .6s linear;
}

.container.service .serviceBlock {
    text-decoration: none;
}

.container.service .serviceBlock .serviceItem .serviceText {
    position: absolute;
    font-size: 64px;
    padding: 45px;
    left: 98px;
    text-align: center;
    width: 700px;
    cursor: pointer;
    color: white;
}

.container.service .serviceBlock .serviceItem .serviceText.serviceSpecial {
    position: absolute;
    font-size: 64px;
    padding: 25px;
    left: 70px;
    width: 800px;
}

.container.service .serviceBlock .serviceItem .serviceText img:nth-child(1) {
    position: absolute;
    top: 10px;
    left: 100px;
    background-color: transparent;
    opacity: 1;
}

.container.service .serviceBlock .serviceItem .serviceText img:nth-child(2) {
    position: absolute;
    top: 30px;
    left: -10px;
    background-color: transparent;
    opacity: 1;
}

.container.service .serviceBlock .serviceItem .serviceText.serviceSpecial img:nth-child(1) {
    position: absolute;
    top: -10px;
    left: 180px;
    background-color: transparent;
    opacity: 1;
}

.container.service .serviceBlock .serviceItem .serviceText.serviceSpecial img:nth-child(2) {
    position: absolute;
    top: 30px;
    left: -10px;
    background-color: transparent;
    opacity: 1;
}

/* info */

.container.info {
    display: flex;
    color: black;
    padding: 10px;
    width: 100%;
    height: 800px;
    background-image: url(../../Pictures/intro/BackgroundInfo.png);
    margin-top: 140px;
}

.container.info .inctructors {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 650px;
    margin-left: 40px;
}

.container.info .inctructors .titletext {
    font-size: 48px;
    font-weight: 600;
    margin-bottom: 65px;
}

.container.info .inctructors .textinfo {
    font-size: 32px;
    font-weight: 600;
    margin-bottom: 65px;
    line-height: 40px;
}

.container.info .orgInfo {
    width: 900px;
    margin-left: 650px;
}

.container.info .orgInfo .titletext {
    display: flex;
    font-size: 20px;
    width: 100%;
}

.container.info .orgInfo .titletext .textblock:nth-child(1) {
    text-align: right;
    margin: 0 15px;
    width: 500px;
}

.container.info .orgInfo .titletext .textblock:nth-child(2) {
    margin-right: 80px;
    line-height: 25px;
    width: 40%;
}


.container.info .orgInfo .titletext .textblock .icon {
    cursor: pointer;
    margin-top: 30px;
}

/* map */

.map {
    margin: 50px 0 50px 125px;
}

/* icons */

.icon {
    margin: 5px;
    color: white;
    background-color: black;
    width: 40px;
    height: 40px;
    padding: 10px;
    border-radius: 50%;
    transition: all 0.5s linear;
}

.icon:hover {
    color: black;
    background-color: white;
    transition: all 0.5s linear;
}

/* App */

.container.App {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 24px;
}

.container.App .titleApp {
    font-size: 48px;
    margin: 250px 0 40px 0;
}

.container.App .textApp {
    width: 500px;
    text-align: center;
    margin-bottom: 100px;
}

/* footer */

.container.footer {
    margin-top: 150px;
    background-color: #D9D9D9;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 150px;
}

.container.footer img {
    margin-right: 25px;
}

.slide {
    margin-top: 157px;
    padding-left: 100px;
}

@keyframes leftAnim {
    0% {

        transform: translateX(-500px);
    }

    100% {

        transform: translateX(0px);
    }
}

@keyframes upAnim {
    0% {

        transform: translateY(100px);
    }

    100% {

        transform: translateY(0px);
    }
}

@keyframes opacity {
    0% {
        opacity: 0;

    }

    100% {
        opacity: 1;

    }
}


@keyframes width {
    0% {
        width: 0px;
    }

    100% {
        width: 300px;
    }
}

